.footer-container {
  position: fixed;
  flex-wrap: wrap;
  left: 0;
  right: 0;
  bottom: 0;
  // height: 2em;
  display: flex;
  justify-content: space-between;
  background: $globalLinearGradient3;
  align-items: center;
}

.footer-elem-1 {
  margin-left: 1ch;
  margin-right: 1ch;
  color: $colorNeonBlue;
  // font-family: $specialHeadingFontFamily, $codeFontFamily;
}

.footer-elem-2 {
  margin-left: 1ch;
  margin-right: 1ch;
  color: $colorNeonTeal;
  // font-family: $specialHeadingFontFamily, $codeFontFamily;
}

.footer-p {
  margin-bottom: 40px;
}

@media screen and (min-width: 400px) {
  .footer-p {
    margin-bottom: 10px;
  }
}