// Header w/ animated background color.

h1, h2, h3, h4, h5, h6 {
  $gradient: linear-gradient(45deg, #f66352, purple, #ee6352);

  font-weight: 900;
  text-transform: uppercase;
  background-image: $gradient;
  -webkit-background-clip: text;
  color: transparent;
  background-size: 400%;
  animation: animatedHeadingBgAnimation 10s 2 alternate;
}

@keyframes animatedHeadingBgAnimation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}
