// ┏━━━━━━━━━━━━━┓
// ┃ Victor Mono ┃
// ┛             ┗━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━━
// Regular (< 500).
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-Italic.ttf") format("truetype");
  font-style: italic
}
// Medium (500).
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-Medium.ttf") format("truetype");
  font-weight: 500
}
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-MediumItalic.ttf") format("truetype");
  font-weight: 500;
  font-style: italic
}
// Bold (700).
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-Bold.ttf") format("truetype");
  font-weight: 700
}
@font-face {
  font-family: "Victor Mono";
  src: url("/assets/victor_mono/VictorMono-BoldItalic.ttf") format("truetype");
  font-weight: 700;
  font-style: italic
}

