// For more code color schemes check out https://github.com/richleland/pygments-css
// Pick one that you like, and just copy / paste it in here (don't delete the first
// .highlight{ ... } block though.

//
// This sets the background color for code blocks. It is applied to both <pre> and <code>.
//
.highlighter-rouge {
  //border-radius: $global-border-radius;
  //background: $syntax-highlight-background-color;
}

.highlight {
  @extend %vertical-rhythm !optional; // Add margin-bottom.
  //background: #272822;
  //color: #f8f8f2;
  //border: unset; // https://blog.logrocket.com/css-inheritance-inherit-initial-unset-and-revert/#initial
  border-radius: $globalBorderRadius;
  //margin: $global-padding;
  //padding: $global-padding;
}

// Applies to code blocks.
.highlight > pre {
  padding: $globalPadding;
  margin-bottom: 0; // Remove margin-bottom
}

// The rouge syntax highlighter (used in _config.yaml) nests these for some reason. This rule
// targets the nested element w/ the style.
.highlight .highlight {
  border: $globalBorderStyle;
}

// For code blocks that are copied as HTML & then pasted from a terminal need to be wrapped in this.
.pre-manual-highlight {
  border: $globalBorderStyle;
  padding: $globalPadding;
}

